/* Menu */
.wrapper {
  padding-left: 230px;
  transition: all 0.4s ease 0s;
}

.page-content-wrapper {
  width: 100%;
}

.sidebar-wrapper {
  margin-top: 50px;
  margin-left: -230px;
  left: 230px;
  width: 230px;
  position: fixed;
  height: 100%;
  overflow-y: auto;
  z-index: 1000;
  transition: all 0.4s ease 0s;
  &.default {
    background-color: @navbar-default-bg;
    border-color: @navbar-default-border;
  }
  &.inverse {
    background-color: @navbar-inverse-bg;
    border-color: @navbar-inverse-border;
  }
}

.navbar-brand {
  text-align: right;
  width: 230px;
  font-weight: bold;
  span {
    &.baa {
      color: @brand-addon-bg-a;
    }
    &.bab {
      color: @brand-addon-bg-b;
    }
    &.bac {
      color: @brand-addon-bg-c;
    }
  }
}

.sidebar-wrapper {
  .sidebar-nav {
    position: absolute;
    top: 0;
    width: 230px;
    list-style: none;
    margin: 0;
    padding: 0;
    li,
    li ul li {
      line-height: 36px;
      text-indent: 7px;
    }
    li a {
      display: block;
      text-decoration: none;
      text-align: right;
      border-bottom: 1px solid;
      padding-right: 20px;
      text-transform: uppercase;
      position: relative;
      cursor: pointer;
    }
    li a i.fa-chevron-circle-right {
      display: none;
    }
    li.active a i.fa-chevron-circle-right {
      display: inline;
    }
    li a i.caret-icon {
      margin-right: 12px;
    }
    li a:active,
    li a:focus {
      text-decoration: none;
    }
    li ul {
      display: none;
      margin: 0;
      padding: 0;
      transition: all 0.4s ease 0s;
    }
    li ul li a {
      padding-left: 10px;
    }
    li.open ul {
      display: block;
    }
    > .sidebar-brand {
      height: 65px;
      line-height: 60px;
      font-size: 18px;
      text-transform: uppercase;
    }
  }
  &.default {
    .sidebar-nav {
      > a,
      > li a {
        color: @navbar-default-link-color;
        border-color: @navbar-default-border;
      }
      li a:hover,
      li.active a:hover {
        color: @navbar-default-link-active-color;
        background-color: darken(@navbar-default-bg, 10%);
        text-decoration: none;
      }
      li.active a {
        color: @navbar-default-link-active-color;
        background: darken(@navbar-default-bg, 10%);
      }
      .sidebar-brand {
        color: @navbar-default-link-color;
      }
    }
  }
  &.inverse {
    .sidebar-nav {
      > a,
      > li a {
        color: @navbar-inverse-link-color;
        border-color: @navbar-inverse-border;
      }
      li a:hover,
      li.active a:hover {
        color: @navbar-inverse-link-hover-color;
        background-color: lighten(@navbar-inverse-bg, 10%);
        text-decoration: none;
      }
      li.active a {
        color: @navbar-inverse-link-hover-color;
        background: lighten(@navbar-inverse-bg, 10%);
      }
      .sidebar-brand {
        color: @navbar-inverse-link-color;
      }
    }
  }
}

/* Boxes */
.box {
  color: @box-addon-color;
  a {
    color: @box-addon-color;
  }
  .box-inverse {
    background-color: @navbar-inverse-bg;
  }
  .box-primary {
    background-color: @brand-primary;
  }
  .box-default {
    color: @text-color;
    background-color: @navbar-default-bg;
    a {
      color: @text-color;
    }
  }
  .box-success {
    background-color: @brand-success;
  }
  .box-info {
    background-color: @brand-info;
  }
  .box-warning {
    background-color: @brand-warning;
  }
  .box-danger {
    background-color: @brand-danger;
  }
}

/** Widgets **/
.widget {
  position: relative;
  margin-bottom: 10px;
  border: 1px solid @table-border-color;
  p {
    font-size: 100%;
  }
  .widget-extra,
  .widget-extra-full {
    position: relative;
    padding: 15px;
  }
  .widget-extra {
    padding-top: 1px;
    padding-bottom: 1px;
  }
  .widget-content-light {
    color: #fff;
    small {
      color: @table-border-color;
    }
  }
  .widget-icon,
  .widget-image {
    width: 64px;
    height: 64px;
  }
  .widget-icon {
    display: inline-block;
    font-size: 28px;
    line-height: 64px;
    color: #fff;
    text-align: center;
    border-radius: 32px;
    .fi,
    .gi,
    .hi,
    .si {
      margin-top: -3px;
    }
  }
  .widget-icon-simple {
    &.widget-icon-right-bottom {
      position: absolute;
      right: 15px;
      bottom: 10px;
    }
    &.widget-icon-right-top {
      position: absolute;
      top: 20px;
      right: 15px;
    }
  }
  .widget-options,
  .widget-options-left {
    position: absolute;
    top: 5px;
    filter: alpha(opacity=50);
    opacity: .5;
  }
  .widget-options {
    right: 5px;
  }
  .widget-options-left {
    left: 5px;
  }
  &.widget-default {
    background-color: @btn-default-bg;
    border-color: @btn-default-border;
    a {
      color: @text-color;
    }
    p {
      color: @text-color;
    }
    .widget-icon {
      color: @btn-default-border;
    }
    .widget-content {
      small {
        color: lighten(@btn-default-border, 40%);
      }
    }
  }
  &.widget-primary {
    background-color: @brand-primary;
    border-color: darken(@brand-primary, 10%);
    a {
      color: #fff;
    }
    p {
      color: #fff;
    }
    .widget-icon {
      color: @brand-primary;
    }
    .widget-content {
      small {
        color: lighten(@brand-primary, 40%);
      }
    }
  }
  &.widget-info {
    background-color: @brand-info;
    border-color: darken(@brand-info, 10%);
    a {
      color: #fff;
    }
    p {
      color: #fff;
    }
    .widget-icon {
      color: @brand-info;
    }
    .widget-content {
      small {
        color: lighten(@brand-info, 40%);
      }
    }
  }
  &.widget-warning {
    background-color: @brand-warning;
    border-color: darken(@brand-warning, 10%);
    a {
      color: #fff;
    }
    p {
      color: #fff;
    }
    .widget-icon {
      color: @brand-warning;
    }
    .widget-content {
      small {
        color: lighten(@brand-warning, 40%);
      }
    }
  }
  &.widget-danger {
    background-color: @brand-danger;
    border-color: darken(@brand-danger, 10%);
    a {
      color: #fff;
    }
    p {
      color: #fff;
    }
    .widget-icon {
      color: @brand-danger;
    }
    .widget-content {
      small {
        color: lighten(@brand-danger, 40%);
      }
    }
  }
  &.widget-success {
    background-color: @brand-success;
    border-color: darken(@brand-success, 10%);
    a {
      color: #fff;
    }
    p {
      color: #fff;
    }
    .widget-icon {
      color: @brand-success;
    }
    .widget-content {
      small {
        color: lighten(@brand-success, 40%);
      }
    }
  }
  .widget-info-data {
    margin-top: 15px;
    font-size: 26px;
    line-height: 30px;
  }
  .widget-options-left:hover,
  .widget-options:hover {
    filter: alpha(opacity=100);
    opacity: 1;
  }
  .widget-simple {
    padding: 15px;
    &:after,
    &:before {
      display: table;
      content: " ";
    }
    &:after {
      clear: both;
    }
    .widget-icon,
    .widget-image {
      margin: 0 15px;
    }
    .widget-icon.pull-left,
    .widget-image.pull-left {
      margin-left: 0;
    }
    .widget-content {
      margin: 12px 0;
      font-size: 18px;
    }
    .widget-content small {
      display: block;
      margin-top: 7px;
      font-size: 13px;
      font-weight: 400;
    }
    .widget-icon.pull-right,
    .widget-image.pull-right {
      margin-right: 0;
    }
  }
  .widget-advanced {
    .widget-header {
      position: relative;
      height: 150px;
      padding: 15px 15px 50px;
      overflow: hidden;
    }
    .widget-background {
      position: absolute;
      top: 0;
      left: 0;
      height: 150px;
    }
    .widget-background-map {
      width: 100%;
      height: 180px;
    }
    .widget-content-image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding: 15px;
      margin: 0;
      background: url(../img/template/ie8_opacity_dark_60.png) repeat;
      background: rgba(0, 0, 0, .6);
    }
    .widget-main {
      position: relative;
      padding: 50px 15px 15px;
    }
    .widget-image-container {
      position: absolute;
      top: -36px;
      left: 50%;
      display: inline-block;
      width: 74px;
      height: 74px;
      padding: 5px;
      margin-left: -36px;
      background-color: #fff;
      border-radius: 36px;
    }
    .widget-header .widget-image-container {
      position: static;
      top: auto;
      left: auto;
      margin: 0;
    }
  }
  &.widget-default .widget-icon,
  &.widget-primary .widget-icon,
  &.widget-info .widget-icon,
  &.widget-warning .widget-icon,
  &.widget-danger .widget-icon,
  &.widget-success .widget-icon {
    background-color: #fff;
  }
  &.widget-default .widget-content,
  &.widget-default .widget-info-data,
  &.widget-default .widget-icon-simple,
  &.widget-primary .widget-content,
  &.widget-primary .widget-info-data,
  &.widget-primary .widget-icon-simple,
  &.widget-info .widget-content,
  &.widget-info .widget-info-data,
  &.widget-info .widget-icon-simple,
  &.widget-warning .widget-content,
  &.widget-warning .widget-info-data,
  &.widget-warning .widget-icon-simple,
  &.widget-danger .widget-content,
  &.widget-danger .widget-info-data,
  &.widget-danger .widget-icon-simple,
  &.widget-success .widget-content,
  &.widget-success .widget-info-data,
  &.widget-success .widget-icon-simple {
    color: #fff;
  }
  .widget-advanced-alt {
    .widget-header,
    .widget-main {
      padding: 15px;
    }
    .widget-header {
      height: auto;
      min-height: 150px;
    }
  }
}

