body {
  -webkit-font-smoothing: antialiased;
}

#logo {
  background-image: url('/img/logo-default.png');
  background-repeat: no-repeat;
  background-position: center right;
  display: block;
  float: left;
  padding: 8px;
  margin: 11px 10px 11px 0px;
  width: 24px;
  height: 25px;
}

.navbar-inverse #logo {
  background-image: url('/img/logo-inverse.png');
}

.pagination-info {
  display: inline-block;
  margin: 20px 0;
  padding: 8px 0px;
}
.no-padding {
  padding: 0;
}
.no-margin {
  margin: 0;
}
ul.typeahead {
  position: absolute;
}

label.error {
  color: @brand-danger;
  display: block;
  font-size: 12px;
}

.center {
  text-align: center;
}

.nowrap,
.clip,
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
}

.nowrap {
  width: 100%;
  display: block;
}

.clip {
  text-overflow: clip;
}

.ellipsis {
  text-overflow: ellipsis;
}

.login-header {
  text-align: center;
  text-transform: uppercase
}

.messages {
  margin-top: 60px;
}

/* splash */
.splash {
  padding: 1em;
  color: @navbar-default-color;
  background: @navbar-default-bg;
  background-attachment: fixed;
  margin-top: -10px;
  &.splash-inverse {
    color: @navbar-inverse-color;
    background: @navbar-inverse-bg;
  }
  &.splash-default p a {
    color: @navbar-default-link-color;
  }
  &.splash-default .brand-text {
    color: @navbar-default-brand-color;
  }
  &.splash-inverse p a {
    color: @navbar-inverse-link-color;
  }
  &.splash-inverse .brand-text {
    color: @navbar-inverse-brand-color;
  }
}

/* Icons */
.fa {
  &.fa-primary {
    color: @brand-primary;
  }
  &.fa-success {
    color: @brand-success;
  }
  &.fa-info {
    color: @brand-info;
  }
  &.fa-warning {
    color: @brand-warning;
  }
  &.fa-danger {
    color: @brand-danger;
  }
  &.fa-default {
    color: @text-color;
  }
}

/* Buttons */
.btn-inverse {
  color: #fff;
  background-color: #555;
  border-color: #3c3c3c;
  &:active,
  &.active {
    background-image: none;
  }
  &:hover,
  &:focus,
  &.focus,
  &:active,
  &.active {
    color: #fff;
    background-color: #3c3c3c;
    border-color: #1d1d1d;
  }
  &.disabled,
  &.disabled:hover,
  &.disabled:focus,
  &.disabled.focus,
  &.disabled:active,
  &.disabled.active,
  &[disabled],
  &[disabled]:hover,
  &[disabled]:focus,
  &[disabled].focus,
  &[disabled]:active,
  &[disabled].active {
    background-color: #555;
    border-color: #3c3c3c;
  }
}

fieldset[disabled] {
  .btn-inverse {
    &:hover,
    &:focus,
    &:active,
    &.active {
      background-color: #555;
      border-color: #3c3c3c;
    }
  }
}

.open > .btn-inverse.dropdown-toggle {
  color: #fff;
  background-color: #3c3c3c;
  border-color: #1d1d1d;
  background-image: none;
}

.btn-inverse.disabled, .btn-inverse.disabled:hover, .btn-inverse.disabled:focus, .btn-inverse.disabled.focus, .btn-inverse.disabled:active, .btn-inverse.disabled.active, .btn-inverse[disabled], .btn-inverse[disabled]:hover, .btn-inverse[disabled]:focus, .btn-inverse[disabled].focus, .btn-inverse[disabled]:active, .btn-inverse[disabled].active, fieldset[disabled] .btn-inverse, fieldset[disabled] .btn-inverse:hover, fieldset[disabled] .btn-inverse:focus, fieldset[disabled] .btn-inverse.focus, fieldset[disabled] .btn-inverse:active, fieldset[disabled] .btn-inverse.active {
  background-color: #555;
  border-color: #3c3c3c;
}

.btn-inverse .badge {
  color: #555;
  background-color: #fff;
}

.btn-admin {
  font-size: 19px;
  line-height: 2;
  border-radius: 0;
  text-align: center;
  i.fa-2x {
    margin: 10px auto 0;
  }
  span {
    text-transform: uppercase;
    font-size: 13px;
  }
}

.btn-clear {
  color: @btn-default-border;
  background-color: transparent;
  border: 1px solid @btn-default-border;
  &:hover {
    color: darken(@btn-default-border, 20%);
    border-color: darken(@btn-default-border, 20%);
  }
}

.btn-any,
.btn-no,
.btn-yes {
  font-weight: @btn-font-weight;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  border: 1px solid @btn-default-border;
}

.btn-yes {
  color: @btn-default-color;
  background-color: @btn-default-bg;
  &.active,
  &:focus,
  &:hover {
    color: white;
  }
  &.active {
    background-color: @btn-success-bg;
    border-color: @btn-success-border;
  }
  &:hover {
    background-color: darken(@btn-success-bg, 10%) !important;
    border-color: darken(@btn-success-border, 10%);
  }
}

.btn-no {
  color: @btn-default-color;
  background-color: @btn-default-bg;
  &.active,
  &:focus,
  &:hover {
    color: white;
  }
  &.active {
    background-color: @btn-danger-bg;
    border-color: @btn-danger-border;
  }
  &:hover {
    background-color: darken(@btn-danger-bg, 10%) !important;
    border-color: darken(@btn-danger-border, 10%);
  }
}

.btn-any {
  color: @btn-default-color;
  background-color: @btn-default-bg;
  &.active,
  &:focus,
  &:hover {
    color: white;
  }
  &.active {
    background-color: darken(@btn-info-bg, 20%);
  }
  &:hover {
    background-color: darken(@btn-info-bg, 10%) !important;
  }
}

.v-ellip, v\:ellip {
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
  max-width: 100%;
}

input.ajax-loading {
  background-image: url('/img/ajax-small.gif');
  background-repeat: no-repeat;
  background-position: center right;
}

/* Sorting columns */
a.sorter {
  background-image: url('/img/sorting-default.png');
  background-repeat: no-repeat;
  background-position: center left;
  cursor: pointer;
  padding-left: 20px;
}

a.sorter.headerSortUp {
  background-image: url('/img/asc-default.png');
}

a.sorter.headerSortDown {
  background-image: url('/img/desc-default.png');
}

/* responsive */
@media (min-width: 768px) and (max-width: 991px) {
  #logo {
    margin-left: 0;
  }

  .login-header {
    padding-top: 20px;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  #logo {
    margin-left: 0;
  }
}

@media (min-width: 1200px) {
  #logo {
    margin-left: 0;
  }
}

@media (max-width: 767px) {
  #logo {
    margin-left: 15px;
  }

  .navbar-brand {
    margin-left: -15px;
  }

  .login-header {
    padding-top: 20px;
  }
}